body {
  background: linear-gradient(to bottom, #e3f2fd, #3d6d94);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  color: #fff;
  height: 100%;
}

/* Tabuleiro */
.tabuleiro {
  display: grid;
  grid-template-columns: repeat(7, 70px);
  grid-template-rows: repeat(6, 70px);
  gap: 15px;
  background-color: rgba(0, 0, 0, 0.30);
  padding: 30px 30px 40px 30px;
  border-radius: 25px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
  width: fit-content;
}

/* Células */
.celula {
  width: 70px;
  height: 70px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  box-shadow: inset 0 4px 6px rgba(255,255,255,0.25), 0 2px 8px rgba(0,0,0,0.3);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease;
  position: relative;
}

.celula:hover {
  background-color: rgba(255, 255, 255, 0.35);
}


.celula.especial {
  box-shadow: 0 0 15px 5px gold inset, 0 0 12px 3px gold;
}

/* Peças */
.peca {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 3px solid rgba(0,0,0,0.4);
  box-shadow: inset -3px -3px 10px rgba(255,255,255,0.7), inset 3px 3px 8px rgba(0,0,0,0.4);
}

/* Peça amarela */
.peca.Amarelo {
  background: #fff85e;
  box-shadow: inset -3px -3px 8px #fffde7, inset 3px 3px 10px #b2aa00, 0 5px 8px rgba(214,204,0,0.6);
}

/* Peça vermelha */
.peca.Vermelho {
  background: #ff5f5f;
  box-shadow: inset -3px -3px 8px #ffbaba, inset 3px 3px 10px #990000, 0 5px 8px rgba(204,0,0,0.6);
}

.painel-lateral {
  background: linear-gradient(to bottom, rgba(157, 157, 157, 0.3), #4a90e2);
  color: #333333;
  border: 0.5px, rgb(0, 0, 0), solid;
  border-radius: 20px;
  padding: 100px 25px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  font-size: 1em;
  height: 400px;
  margin-top: 100px;
}

.confirmacao p {
  font-size: 1.2em;
  text-align: center;
  margin: 0 0 15px 0;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.4);
  color:black;
}

.botao-container {
  display: inline-block;   
  width: auto;             
  padding: 0;
  margin: 0;
}

.botao-container button {
  padding: 12px 28px;
  font-size: 1.1em;
  cursor: pointer;
  border-radius: 12px;
  border: none;
  background-color: #4a90e2;
  color: black;
  font-weight: 600;
  box-shadow: 0 5px 15px rgba(74,144,226,0.4);
  transition: background-color 0.3s ease;
  position: relative;
  bottom: 90px;
  left: 70px;
  
}


.celula.destaque {
  background-color: rgba(255, 255, 255, 0.45);
  box-shadow: 0 0 12px 3px #4fc1ff inset;
}

 .tabuleiro-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

