.linha-seta {
  display: grid;
  grid-template-columns: repeat(7, 85px);
  height: 30px;
  margin-bottom: 10px;
  margin-right: 90px;
  margin-left: 90px;
}

.celula-seta {
  display: flex;
  justify-content: center;
  align-items: center;
}

.seta {
  font-size: 30px;
  color: rgba(40, 97, 184, 0.7);
  
}





