.ContB{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

}

.tela-boasvindas {

  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  width: 320px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 200px;
}

.tela-boasvindas h1 {
  
  font-size: 24px;
  margin-bottom: 16px;
  text-align: center;
  color: #333333;
}

.tela-boasvindas form {
  
  display: flex;
  flex-direction: column;
}

.tela-boasvindas input,
.tela-boasvindas select {
  
  width: 100%;
  padding: 8px;
  font-size: 14px;
  border: 1px solid #cccccc;
  border-radius: 4px;
  margin-bottom: 12px;
}

.tela-boasvindas button {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  background-color: #1976d2;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.tela-boasvindas button:hover {
  background-color: #0d47a1;
}

.tela-boasvindas {
  
  animation: fadeIn 1s ease-in-out;
  background: #ffffff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 10px 10px rgba(0, 0, 0, 0.2);
  width: 360px;
  transition: transform 0.3s;
}

.tela-boasvindas:hover {
  
  transform: scale(1.02);
}

.tela-boasvindas h1 {
  
  font-size: 28px;
  margin-bottom: 20px;
  color: #0d47a1;
  font-weight: bold;
  letter-spacing: 1px;
}

.tela-boasvindas input,
.tela-boasvindas select {
  background-color: #f0f7ff;
  border: 1px solid #90caf9;
  transition: 0.3s;
}

.tela-boasvindas input:focus,
.tela-boasvindas select:focus {
  outline: none;
  border-color: #1976d2;
  box-shadow: 0 0 5px #64b5f6;
}

.tela-boasvindas button {
  background-color: #1976d2;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.tela-boasvindas button:hover {
  background-color: #d3aa3a;
}

body {
  background: linear-gradient(to bottom, #e3f2fd, #3d6d94); 
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  height: 100vh;
}

